define('platform/commonUI/mobile/bundle',[
    "./src/MCTDevice",
    "./src/AgentService",
    "./src/DeviceClassifier",
    'legacyRegistry'
], function (
    MCTDevice,
    AgentService,
    DeviceClassifier,
    legacyRegistry
) {

    legacyRegistry.register("platform/commonUI/mobile", {
        "extensions": {
            "directives": [
                {
                    "key": "mctDevice",
                    "implementation": MCTDevice,
                    "depends": [
                        "agentService"
                    ]
                }
            ],
            "services": [
                {
                    "key": "agentService",
                    "implementation": AgentService,
                    "depends": [
                        "$window"
                    ]
                }
            ],
            "runs": [
                {
                    "implementation": DeviceClassifier,
                    "depends": [
                        "agentService",
                        "$document"
                    ]
                }
            ]
        }
    });
});

